/**
 * utils-request
 * @description api模块管理，loading配置，请求拦截，错误处理
 */
import { getCurrentInstance } from 'vue'; 
import Request from 'luch-request';
import { baseUrl, apiPath ,apiModel} from '@/utils/config';
import $store from '@/store';
import md5 from 'js-md5'

// base64 编码（兼容小程序端无 window.btoa）
function base64Encode(str = '') {
  try {
    // #ifdef H5
    return window.btoa(str);
    // #endif
  } catch (e) {}

  // #ifndef H5
  const utf8ToBytes = (s) => {
    const bytes = [];
    for (let i = 0; i < s.length; i++) {
      let codePoint = s.charCodeAt(i);
      if (codePoint < 0x80) {
        bytes.push(codePoint);
      } else if (codePoint < 0x800) {
        bytes.push(0xc0 | (codePoint >> 6));
        bytes.push(0x80 | (codePoint & 0x3f));
      } else {
        bytes.push(0xe0 | (codePoint >> 12));
        bytes.push(0x80 | ((codePoint >> 6) & 0x3f));
        bytes.push(0x80 | (codePoint & 0x3f));
      }
    }
    return new Uint8Array(bytes);
  };
  const buf = utf8ToBytes(str).buffer;
  // wx/uni 都提供 arrayBufferToBase64
  // eslint-disable-next-line no-undef
  if (typeof wx !== 'undefined' && wx.arrayBufferToBase64) return wx.arrayBufferToBase64(buf);
  if (typeof uni !== 'undefined' && uni.arrayBufferToBase64) return uni.arrayBufferToBase64(buf);
  return '';
  // #endif
}
const options = {
  // 显示操作成功消息 默认不显示
  showSuccess: false,
  // 成功提醒 默认使用后端返回值
  successMsg: '',
  // 显示失败消息 默认显示
  showError: true,
  // 失败提醒 默认使用后端返回信息
  errorMsg: '',
  // 显示请求时loading模态框 默认显示
  showLoading: true,
  // loading提醒文字
  loadingMsg: '加载中',
  // 需要授权才能请求 默认放开
  auth: false,
  // ...
};

// Loading全局实例
let LoadingInstance = {
  target: null,
  count: 0,
};

/**
 * 关闭loading
 */
function closeLoading() {
  if (LoadingInstance.count > 0) LoadingInstance.count--;
  if (LoadingInstance.count === 0) uni.hideLoading();
}

/**
 * @description 请求基础配置 可直接使用访问自定义请求
 */
const http = new Request({
  baseURL:  baseUrl+apiModel,
  timeout: 8000,
  method: 'GET',
  // #ifdef APP-PLUS
  sslVerify: false,
  // #endif
  // #ifdef H5
  // 跨域请求时是否携带凭证（cookies）仅H5支持（HBuilderX 2.6.15+）
  withCredentials: false,
  // #endif
  custom: options,
});

/**
 * @description 请求拦截器
 */
http.interceptors.request.use(
  (config) => {
	// 兼容：小程序“重新打开/刷新”时 store 可能尚未恢复 isLogin，但本地 token 已存在
	const token = uni.getStorageSync('token');
	if (token && !$store('user').isLogin) {
	  $store('user').setToken(token);
	}
    if (config.custom.auth && !token && !$store('user').isLogin) {
      // 这里做未登录拦截
      return Promise.reject();
    }
    if (config.custom.showLoading) {
      LoadingInstance.count++;
      LoadingInstance.count === 0 &&
        uni.showLoading({
          title: config.custom.loadingMsg,
          mask: true,
          fail: () => {
            uni.hideLoading();
          },
        });
    }
	// 判断本地是否存在token，如果存在则带上请求头
	let timestamp = Date.parse(new Date().toString())/1000;
	 const passstr=md5(import.meta.env.GF_API_SECRET+timestamp)
	let baseheader = {
		 Accept: 'text/json',
		'Content-Type': 'application/json;charset=UTF-8',
		'Businessid': import.meta.env.GF_BUSINESUSSID,
		'apiverify': base64Encode(passstr+"#"+timestamp),
	};
	if (token) {
		baseheader=Object.assign({},baseheader,{
			Authorization: `${token}`	// 这里是token(可自行修改)
		})
	  }
	config.header =baseheader;//请求头
    return config;
  },
  (error) => {
    return Promise.reject(error);
  },
);

/**
 * @description 响应拦截器
 */
http.interceptors.response.use(
  (response) => {
    // 自动设置登陆令牌
    if (response.header.authorization || response.header.Authorization) {
      $store('user').setToken(response.header.authorization || response.header.Authorization);
    }

    response.config.custom.showLoading && closeLoading();
    if (response.data.code !== 0) {
      if (response.config.custom.showError)
        uni.showToast({
          title: response.data.message || '服务器开小差啦,请稍后再试~',
          icon: 'none',
          mask: true,
        });
      return Promise.resolve(response.data);
    }
    if (
      response.data.code !== 0 &&
      response.data.message !== '' &&
      response.config.custom.showSuccess
    ) {
      uni.showToast({
        title: response.config.custom.successMsg || response.data.message,
        icon: 'none',
      });
    }
	if(response.data&& response.data.token){
		const userStore = $store('user');
		userStore.setToken(response.data.token)
	}
    return Promise.resolve(response.data);
  },
  (error) => {
    const userStore = $store('user');
    const isLogin = userStore.isLogin;
    let errorMessage = '网络请求出错';
    if (error !== undefined) {
      switch (error.statusCode) {
        case 400:
          errorMessage = '请求错误';
          break;
        case 401:
          if (isLogin) {
            errorMessage = '您的登陆已过期';
          } else {
            errorMessage = '请先登录';
          }
          userStore.logout(true);
          break;
        case 403:
          errorMessage = '拒绝访问';
          break;
        case 404:
          errorMessage = '请求出错';
          break;
        case 408:
          errorMessage = '请求超时';
          break;
        case 429:
          errorMessage = '请求频繁, 请稍后再访问';
          break;
        case 500:
          errorMessage = '服务器开小差啦,请稍后再试~';
          break;
        case 501:
          errorMessage = '服务未实现';
          break;
        case 502:
          errorMessage = '网络错误';
          break;
        case 503:
          errorMessage = '服务不可用';
          break;
        case 504:
          errorMessage = '网络超时';
          break;
        case 505:
          errorMessage = 'HTTP版本不受支持';
          break;
      }
      // if (error.errMsg.includes('timeout')) errorMessage = '请求超时';
      // #ifdef H5
      // if (error.errMsg.includes('Network'))
      //   errorMessage = window.navigator.onLine ? '服务器异常' : '请检查您的网络连接';
      // #endif
    }

    if (error && error.config) {
      if (error.config.custom.showError === false) {
        uni.showToast({
          title: error.data?.message || errorMessage,
          icon: 'none',
          mask: true,
        });
      }
      error.config.custom.showLoading && closeLoading();
    }

    return false;
  },
);

const request = (config) => {
  // -------- URL 规范化：避免出现 /uniapp/uniapp/*、/business/business/* ----------
  // 场景：baseURL 已包含 apiModel(/uniapp)，而调用方 url 也写了 /uniapp/xxx，拼接后变成 /uniapp/uniapp/xxx
  if (config && typeof config.url === 'string') {
    const url = config.url;
    const model = typeof apiModel === 'string' ? apiModel : '';
    const pathPrefix = typeof apiPath === 'string' ? apiPath : '';
    // 去掉重复的 apiModel 前缀
    if (model && model.startsWith('/') && url.startsWith(model + '/')) {
      config.url = url.slice(model.length);
    }
    // 去掉重复的 apiPath 前缀
    if (pathPrefix && pathPrefix.startsWith('/') && config.url.startsWith(pathPrefix + '/')) {
      config.url = config.url.slice(pathPrefix.length);
    }
    // 常见重复：/business/business/*
    if (config.url.startsWith('/business/business/')) {
      config.url = config.url.replace('/business/business/', '/business/');
    }
    // 常见重复：/uniapp/uniapp/*
    if (config.url.startsWith('/uniapp/uniapp/')) {
      config.url = config.url.replace('/uniapp/uniapp/', '/uniapp/');
    }
  }

  // 兼容接口前缀：你期望后端路径为 /business/xxx/yyy
  // 若环境变量未配置 apiModel/apiPath 的 /business 前缀，则这里兜底补齐。
  // 只对业务接口做前缀补齐，避免影响绝对地址或第三方请求。

  const needBusinessPrefix =
    (typeof apiModel !== 'string' || !apiModel.includes('/business')) &&
    (typeof apiPath !== 'string' || !apiPath.startsWith('/business'));
  if (needBusinessPrefix && typeof config.url === 'string' && config.url.startsWith('/') && !config.url.startsWith('/business/')) {
    config.url = '/business' + config.url;
  }

  if (config.url[0] !== '/') {
     config.url = apiPath + config.url;
  }
  return http.middleware(config);
};

export default request;
